/* 
  HTML, BODY, global
  ======================================= */
html,
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: brandon-grotesque, sans-serif;
  color: var(--white, #ffffff);
}

/* 
  Headings 
  ======================================= */
.nabshow_2021 h1,
.nabshow_2021 h2,
.nabshow_2021 h3,
.nabshow_2021 h4,
.nabshow_2021 h5,
.nabshow_2021 h6,
.nabshow_2021 p,
.nabshow_2021 blockquote,
.nabshow_2021 li {
  font-family: brandon-grotesque, sans-serif;
  color: var(--white, #ffffff);
}

@media (min-width: 48em) {
  .h-xxl {
    font-weight: 900;
    font-size: 4.375rem;
    line-height: 4.375rem;
    /*content:"768px";*/
  }
}

.h-xl, .feature-text h1,
.feature-text h2, .generic h1, .wysiwyg-typography h1,
.wysiwyg-typography h2 {
  font-weight: 900;
  font-size: 1.25rem;
  line-height: 1.875rem;
  padding-bottom: 1.25rem;
}
