/* 
  Quotes
  =======================================
  Blockquotes etc
  ======================================= */
.pullquote {
  position: relative;
  padding: 2.5rem 0;
  text-align: center;
}
.pullquote::before, .pullquote::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(285.1deg, rgba(158, 69, 147, 0) -15.62%, var(--primary-light, #9e4593) 146.86%);
  width: 7.8125rem;
  height: 0.3125rem;
}
.pullquote::before {
  top: 0;
}
.pullquote::after {
  bottom: 0;
}
@media (min-width: 48em) {
  .pullquote {
    text-align: left;
    /*content:"768px";*/
  }
  .pullquote::before, .pullquote::after {
    left: 0;
    transform: translateX(0);
  }
}
.pullquote p {
  font-weight: 900;
  font-size: 1.5625rem;
  line-height: 2.5rem;
  text-transform: uppercase;
  padding-bottom: 1.25rem;
}
@media (min-width: 48em) {
  .pullquote p {
    font-weight: 900;
    font-size: 1.9375rem;
    line-height: 2.75rem;
    /*content:"768px";*/
  }
}
.pullquote cite {
  font-weight: 900;
  font-size: 0.75rem;
  line-height: 0.75rem;
  letter-spacing: 0.15rem;
  text-transform: uppercase;
  font-style: normal;
}

.wysiwyg-typography {
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 2.875rem;
  color: var(--white, #ffffff);
}
.wysiwyg-typography p,
.wysiwyg-typography li,
.wysiwyg-typography p span {
  color: var(--white, #ffffff);
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.9375rem;
}
@media (min-width: 48em) {
  .wysiwyg-typography p,
.wysiwyg-typography li,
.wysiwyg-typography p span {
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 2.875rem;
    /*content:"768px";*/
  }
}
.wysiwyg-typography ul,
.wysiwyg-typography ol {
  margin-left: 1em;
}
.wysiwyg-typography h1,
.wysiwyg-typography h2 {
  color: var(--white, #ffffff);
  text-transform: uppercase;
}
.wysiwyg-typography h3,
.wysiwyg-typography h4 {
  color: var(--white, #ffffff);
  padding-bottom: 1rem;
}
.wysiwyg-typography p + h3,
.wysiwyg-typography p + h4 {
  margin-top: 1.25rem;
}
.wysiwyg-typography a {
  color: var(--tertiary-color, #f4b2ec);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  scroll-behavior: smooth;
  background-color: var(--primary-dark, #030621) !important;
  color: var(--white, #ffffff);
  font-family: brandon-grotesque, sans-serif;
}
html *,
body * {
  font-family: brandon-grotesque, sans-serif;
}

html {
  background-color: var(--primary-color, #2a173c);
}

.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}
.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  clip-path: none;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: 700;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

broadstreet-zone-container div {
  text-align: center;
}
broadstreet-zone-container broadstreet-zone div > span {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}
broadstreet-zone-container broadstreet-zone div > span:before {
  content: "Advertisement";
  display: block;
  font-family: proxima-nova, sans-serif;
  font-size: 0.75rem;
  color: var(--gray-medium-light, #9e9e9e);
  text-align: center;
  line-height: 1;
  padding-bottom: 0.25rem;
}
broadstreet-zone-container._hidden {
  display: none;
}
.ad._footer broadstreet-zone-container {
  padding-top: 0;
  padding-bottom: 0;
}
.ad._footer broadstreet-zone-container broadstreet-zone div {
  height: 0;
}
.ad._footer broadstreet-zone-container broadstreet-zone div > span:before {
  display: none;
}

.ad._banner {
  margin: 3.75rem auto;
}
@media (min-width: 48em) {
  .ad._banner {
    margin: 7.5rem auto;
    max-width: 51.125rem;
    /*content:"768px";*/
  }
}
.ad._home broadstreet-zone-container div span {
  display: inline-block;
}
@media (min-width: 48em) {
  .ad._home broadstreet-zone-container div span {
    margin-top: 2.5rem;
    /*content:"768px";*/
  }
}
.ad._top broadstreet-zone-container div span {
  display: inline-block;
  margin: 1.25rem 0;
}
.schedule + .ad._top {
  margin: 2.5rem 0 0;
}
@media (min-width: 48em) {
  .schedule + .ad._top {
    margin: 6.25rem 0 0;
    /*content:"768px";*/
  }
}
.ad._tall broadstreet-zone-container div span {
  display: inline-block;
  margin: 0 0 1.25rem 0;
}
@media (min-width: 48em) {
  .ad._tall {
    float: right;
    margin-left: 2rem;
    /*content:"768px";*/
  }
}
.ad._bottom broadstreet-zone-container div span {
  display: inline-block;
  margin-top: 1.875rem;
}
@media (min-width: 48em) {
  .ad._bottom broadstreet-zone-container div span {
    margin-top: 3.125rem;
    /*content:"768px";*/
  }
}
@media (min-width: 64em) {
  .ad._bottom broadstreet-zone-container div span {
    margin-top: 3.75rem;
    /*content:"1024px";*/
  }
}
@media (min-width: 80em) {
  .ad._bottom broadstreet-zone-container div span {
    margin-top: 5rem;
    /*content:"1280px";*/
  }
