.decorative._blur {
  position: relative;
}
.decorative._blur::before, .decorative._blur::after {
  content: "";
  position: absolute;
  width: 33%;
  height: 33%;
  background-image: url("/wp-content/themes/nabshow-lv-child-2021/assets/images/decorative-blur.png");
  background-size: contain;
  background-repeat: no-repeat;
}
.decorative._blur::before {
  top: 0;
  left: 0;
  transform: rotate(180deg);
}
.decorative._blur::after {
  bottom: 0;
  right: 0;
}
.decorative._blur-middle:before, .decorative._blur-middle:after {
  width: 50%;
  height: 50%;
}
.decorative._blur-middle::after {
  bottom: 20%;
  right: 0;
}
.decorative._lightlines-left-side::before {
  display: none;
}
@media (min-width: 48em) {
  .decorative._lightlines-left-side {
    position: relative;
    /*content:"768px";*/
  }
  .decorative._lightlines-left-side::before {
    display: block;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 4rem;
    height: 25rem;
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: 0 -50%;
  }
}
.decorative._lightlines-bottom-right {
  background-repeat: no-repeat;
  background-position: 100% 100%;
  padding-bottom: 3rem;
  background-size: 30% auto;
}
.decorative._lightlines-footer-corner {
  display: none;
}
@media (min-width: 48em) {
  .decorative._lightlines-footer-corner {
    display: block;
    height: 0;
    position: relative;
    /*content:"768px";*/
  }
  .decorative._lightlines-footer-corner::before {
    content: "";
    position: absolute;
    bottom: -7.5rem;
    left: 0;
    width: 15rem;
    height: 14.375rem;
    background-size: cover;
  }
}
.decorative._lightlines-footer-corner._pulled::before {
  left: -6.5rem;
}
.decorative._lightlines-top-left-angle {
  position: relative;
}
@media (min-width: 90em) {
  .decorative._lightlines-top-left-angle {
    /*content:"1440px";*/
  }
  .decorative._lightlines-top-left-angle::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 14rem;
    height: 40rem;
    display: block;
    background-size: contain;
    background-repeat: no-repeat;
  }
}
.decorative._lightlines-strip {
  height: 1.25rem;
  background-size: cover;
}
.decorative._lightlines-footer-strip {
  display: none;
}
@media (min-width: 48em) {
  .decorative._lightlines-footer-strip {
    display: block;
    position: relative;
    /*content:"768px";*/
  }
  .decorative._lightlines-footer-strip::before {
    content: "";
    position: absolute;
    bottom: -7.5rem;
    left: 0;
    right: 0;
    height: 1.25rem;
    background-size: cover;
  }
}

