// FORMS
// ===========================================
// Global form styles
// ===========================================

.field {
  margin-bottom: rem(26);
  @include bp-1024 {
    margin-bottom: rem(30);
  }
}

.field__label {
  display: block;
  color: $color__white;
  font-family: $font__primary;
  font-size: rem(12);
  font-weight: 600;
  margin-bottom: rem(6);
  text-transform: uppercase;
}

.field__required {
  color: $color__pink;
}

.field__error,
.field__hint {
  display: block;
  color: $color__pink;
  font-size: rem(12);
  font-weight: 600;
  margin: rem(12) 0 0;
  text-transform: uppercase;
  a {
    color: $color__pink;
    transition: all 0.3s;
    &:hover,
    &:focus {
      opacity: 0.8;
    }
  }
}

.field__input,
.woocommerce-Input {
  &[type="text"],
  &[type="email"],
  &[type="password"],
  &[type="color"],
  &[type="date"],
  &[type="month"],
  &[type="number"],
  &[type="tel"],
  &[type="time"],
  &[type="url"] {
    background: $color__white;
    border-radius: rem(4);
    border: 1px solid $color__white;
    color: $color__gray-dark;
    font-family: $font__primary;
    font-size: rem(18);
    font-weight: 600;
    line-height: rem(52);
    text-decoration: none;
    padding: 0 rem(14);
    transition: border-color 0.25s;
    width: 100%;
    &:hover {
      border-color: $color__purple;
    }
    &:focus {
      outline: none;
      border-color: $color__pink;
    }
  }
}

.field__input,
.woocommerce-Input {
  &[type="checkbox"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: $color__white;
    border-radius: rem(4);
    border: 1px solid $color__white;
    height: rem(24);
    width: rem(24);
    transition: border-color 0.25s;
    &:hover {
      border-color: $color__purple;
    }
    &:focus {
      outline: none;
      border-color: $color__pink;
    }
    &:checked {
      background: $color__pink;
      background-image: url("data:image/png;base64,");
      background-position: 50% 50%;
      background-size: 75% 75%;
      background-repeat: no-repeat;
    }
  }
}

.field__select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: $color__white;
  background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAyLjExNyAxLjMyMyIgaGVpZ2h0PSI1IiB3aWR0aD0iOCI+PHBhdGggZD0iTTAgMGwxLjA1NyAxLjMyMyAxLjA2LTEuMzI0eiIgZmlsbD0iIzg4OCIvPjwvc3ZnPg==);
  background-repeat: no-repeat;
  background-position: 90% center;
  background-size: rem(8) rem(5);
  border-radius: rem(4);
  border: 1px solid $color__white;
  color: $color__gray;
  font-family: $font__primary;
  font-size: rem(18);
  font-weight: 600;
  line-height: rem(52);
  text-decoration: none;
  padding: 0 rem(28) 0 rem(14);
  height: rem(28);
  line-height: rem(28);
  cursor: pointer;
  transition: border-color 0.25s;
  position: relative;
  &:hover {
    border-color: $color__purple;
  }
  &:focus {
    outline: none;
    border-color: $color__pink;
  }
}

.field__woowrapper {
  color: $color__white;
}

.field__title {
  font-size: rem(20);
  //line-height: rem(40);
  margin-bottom: 0;
}
.field__subtitle {
  font-size: rem(14);
  line-height: 1.1;
  font-style: italic;
  margin-bottom: rem(20);
}

.field__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  li {
    display: block;
    & + li {
      margin-top: rem(20);
    }
  }
}

.field__list-input {
  display: block;
  position: relative;
  padding: rem(4) 0 0 rem(36);
  font-size: rem(16);
  line-height: rem(20);
  input {
    position: absolute;
    top: 0;
    left: 0;
  }
}

.field__list-select {
  @extend .field__list-input;
  padding: rem(4) 0 0 0;
  select {
    margin-right: rem(6);
    vertical-align: middle;
    float: left;
  }
}

.field__optinButtons {
  display: flex;
  flex-flow: row wrap;
  button {
    flex: 0 1 auto;
    margin-right: 1rem;
    margin-bottom: 1rem;
  }
}

.mo-openid-app-icons {
  p {
    display: none;
  }
  a.btn.btn-mo.btn-block.btn-social.btn-customtheme.btn-custom-dec.login-button {
    @extend .button;
    line-height: rem(10);
    @include bp-768 {
      margin-right: rem(32);
    }
    @include bp-1024 {
      margin-right: 0;
    }
    &:last-child {
      margin-top: rem(32);
      @include bp-768 {
        margin-top: 0;
        margin-right: 0;
      }
      @include bp-1024 {
        margin-top: rem(32);
      }
    }
    i {
      border: none;
    }
    .mofa {
      top: rem(-5);
      left: rem(14);
    }
  }
}

// EMAIL SIGNUP
// ===========================================
// Horizontal layout input and submit
// ===========================================

.email-signup {
  border-bottom: rem(2.5) solid $color__white;
  display: grid;
  grid-template-columns: 1fr auto;
}
.email-signup__input,
.nabshow_2021 .mktoForm input#Email.mktoField {
  appearance: none;
  background: none;
  border: none;
  @include primary-black-12-12-20;
  padding: rem(12) rem(12) rem(12) 0;
  color: $color__white;
  &::placeholder {
    color: $color__gray--medium;
  }
}
.nabshow_2021 .mktoForm input#Email.mktoField {
  //overrides :/
  border: 0 !important;
  box-shadow: none !important;
}
.nabshow_2021 .mktoForm .mktoButtonWrap.mktoBlueBox button.mktoButton {
  @extend .button;
  @extend .button, ._arrow;
  @extend .button, ._arrow, ._small;
  //overrides :/
  background-color: transparent !important;
  font-weight: 900 !important;
  font-size: 0.75rem !important;
}
.nabshow_2021 .mktoButtonRow {
  //overrides :/
  margin-top: rem(12) !important;
}

// CUSTOM SELECT
// ===========================================
// nab custom select
// ===========================================

.nab-custom-select {
  .chosen-container,
  .chosen-drop,
  .chosen-single,
  .chosen-container-active.chosen-with-drop .chosen-single {
    width: 100%;
    background: $color__gray;
    box-shadow: none;
    color: #fff;
    border-radius: 0;
  }
  .chosen-single,
  .chosen-container-active.chosen-with-drop .chosen-single {
    border: 0;
    padding: rem(10) rem(20) !important;
    height: auto;
    font-size: rem(20);
  }

  .chosen-container {
    &.chosen-with-drop {
      .chosen-drop {
        border-color: transparent;
        box-shadow: 0 rem(9) rem(35) 0 rgba(64, 67, 71, 0.5);
      }
    }
  }
  .chosen-results {
    background-color: $color__gray;
    color: $color__white;
    padding: 0;
    margin: 0;
    font-size: rem(16);
    li {
      padding: rem(15) rem(35);
      line-height: normal;
      &.highlighted {
        background-color: $color__gray; //#4b4b4b;
        background-image: none;
      }
    }
  }
  .chosen-container-single {
    .chosen-search {
      display: none;
    }
    .chosen-single {
      div {
        width: rem(40);
        display: flex;
        justify-content: center;
        align-items: center;
        right: rem(20);
        b {
          background: none;
          display: inline-block;
          width: 0;
          height: 0;
          border-left: rem(10) solid transparent;
          border-right: rem(10) solid transparent;
          border-top: rem(16) solid #fff;
        }
      }
      span {
        margin-right: rem(40);
      }
    }
  }
  select {
    width: 100%;
    color: $color__black;
  }
  option {
    background: $color__gray;
    &:hover {
      background: $color__gray; //#4b4b4b;
    }
  }

  .select2-container {
    .select2-selection--single {
      height: auto;
      background-color: $color__gray; //#3f3f3f;
    }
    &--default {
      .select2-selection--single {
        border: 0;
        border-radius: rem(19);
        &:focus {
          outline: none;
          box-shadow: none;
        }
        .select2-selection__rendered {
          height: auto;
          line-height: 1;
          color: #fff;
          padding: rem(10) rem(40) rem(10) rem(20);
          font-size: rem(18);
          &:focus {
            outline: none;
            box-shadow: none;
          }
        }
        .select2-selection__arrow {
          top: 50%;
          margin-top: rem(-5);
          right: rem(10);
        }
      }
    }
  }
}

.nab-custom-select
  .select2-container--default
  .select2-selection--single
  .select2-selection__arrow
  b,
.nab-custom-select
  .select2-container--default.select2-container--open
  .select2-selection--single
  .select2-selection__arrow
  b {
  border-width: rem(9) rem(9) 0 rem(9);
  border-color: $color__pink transparent transparent transparent;
  left: 0;
  top: 0;
}
.nab-custom-select .select2-results__option {
  padding: 15px 20px;
}

.select2 {
}
.select-dark {
  .select2-container {
    &.select2-container--focus {
      .select2-selection--single {
        border-color: $color__pink;
      }
    }
    .select2-selection--single {
      background-color: $color__white;
      border: 1px solid $color__white;
      color: $color__gray;
      border-radius: rem(4);
      outline: none;
      transition: border-color 0.25s;
      font-family: $font__primary;
      font-size: rem(18);
      height: rem(52);
      &:hover {
        border-color: $color__purple;
      }

      .select2-selection__arrow {
        height: rem(50);
      }
      .select2-selection__rendered {
        color: $color__white;
        line-height: rem(52);
        .nabshow_signup & {
          color: $color__black;
        }
      }
    }
  }
}

//for signup page
.page-template-sign-up-template-nabshow {
  .select2-dropdown {
    background-color: $color__gray-dark;
    border: 1px solid $color__purple-light;
  }
  .select2-container--default .select2-results__option--selected {
    background-color: $color__gray-dark;
  }
  .select2-container--default
    .select2-results__option--highlighted.select2-results__option--selectable {
    background-color: $color__purple-light;
  }
}

// tagchecks

.tagchecks {
  list-style-type: none;
  // display: grid;
  // grid-gap: rem(8);
  // grid-template-columns: repeat(auto-fill, minmax(13.75rem, 1fr));
  overflow: hidden;
  margin: 0 rem(-32);
  text-align: center;
}
.tagcheck {
  display: inline-block;
  padding: 0 rem(8) rem(16);
  position: relative;
  overflow: hidden;
  text-align: center;
  margin: 0 auto;
}
.tagcheck__input {
  position: absolute;
  left: -100%;
}
.tagcheck__label {
  text-transform: uppercase;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: rem(28);
  @include primary-black-12-12-20;
  line-height: 1.6;
  background: linear-gradient(
      256.59deg,
      rgba(158, 69, 147, 0) 81.26%,
      #9e4593 139.5%
    ),
    linear-gradient(
      243.87deg,
      rgba(12, 165, 234, 0.6) -13.36%,
      rgba(47, 93, 171, 0.12) 31.04%
    );
  cursor: pointer;
  transition: all 0.3s;
  @include bp-960 {
    @include primary-black-16-16-20;
    line-height: 1.6;
  }
  &:hover,
  &:focus {
    opacity: 0.85;
  }
}
.tagcheck__input:checked + .tagcheck__label {
  background: $color__purple-light;
  &:hover,
  &:focus {
    opacity: 1;
  }
}
.tagcheck__label--circle {
  border-radius: 50%;
  width: rem(160);
  height: rem(160);
  @include bp-960 {
    width: rem(220);
    height: rem(220);
  }
}
.tagcheck__label--box {
  border-radius: rem(5);
  padding: rem(20) rem(36);
}
